.delivery-item-cover{
    height: 150px;
    width: 150px;
    background-color: var(--primary-bg);
    box-shadow: var(--collection-shadow);
    border-radius: 50%;
}

.delivery-item-image{
    object-fit: cover;
    height: 100%;
    width: 100%;
    border-radius: 50%;
}

.delivery-item-title{
    font-size: 20px;
    font-weight: 550;
    text-overflow: ellipsis;
    overflow: hidden;
    margin-top: 6px;
    text-align: center;
}